<template>
    <div class="navbar-wrapper">
        <div class="nav-bar" :class="{'home-style':curPath === '/home'}">
            <div class="logo" @click="toPage('home')"><img src="../assets/logog.png" alt=""></div>
            <div class="nav-item-group">
                <div class="nav-item" @click="toPage('socialmedia')" :class="{'active':curPath === '/socialmedia'}">社 群</div>
                <div class="nav-item" @click="toPage('video')" :class="{'active':curPath === '/video'}">影 片</div>
                <div class="nav-item" @click="toPage('shop')" :class="{'active':curPath === '/shop'}">購 物</div>
                <div class="nav-item" @click="toPage('exchange')" :class="{'active':curPath === '/exchange'}">交 換</div>
                <div class="nav-item" @click="toPage('together')" :class="{'active':curPath === '/together'}">湊 團</div>
            </div>
            <div class="user-btn-group">
                <div class="btn add"><img src="../assets/icon/add.svg" alt=""></div>
                <div class="btn user" @click="toMyPage()"><img src="../assets/icon/user.svg" alt=""></div>
                <div class="btn message"><img src="../assets/icon/message.svg" alt=""></div>
                <div class="btn setting"><img src="../assets/icon/setting.svg" alt=""></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    computed:{
        curPath(){
            return this.$route.path
        },
        isLogin(){
            return this.$store.state.user.loginAccount?true:false
        }
    },
    methods:{
        toPage(path){
            this.$router.push(path)
        },
        toMyPage(){
            if (this.isLogin){
                this.toPage('/mine')
            } else {
                this.$showLoginPopup()
            }
        }
    }

}
</script>

